  const [senha, setSenha] = useState("");

  useEffect(() => {
    // getUser();
  });

  const getUser = () => {
    axios({
      method: "get",
      url: "http://localhost/place_backend/pais/getPais.php"
    })
      .then(function(response) {
        console.log("resp get:");
        // console.log(response.data);
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        // console.log(JSON.parse(response.data));
        // console.log(JSON.parse(str[0]));
        // console.log(JSON.parse(data));
        for (let i = 0; i < str.length; i++) {
          let registro = str[0].split(",");
          json.push({
            id: registro[0],
            cpf: registro[1],
            senha: registro[2],
            nome_resp1: registro[3],
            nome_resp2: registro[4],
            tel1: registro[5],
            tel2: registro[6],
            matricula_aluno: registro[7],
            nivel: registro[8]
          });
        }
        console.log(json);
      })
      .catch(function(response) {
        console.log(response);
      });
  };

